<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('TEMPLATE_NAME') }}</label>
          <vs-input class="w-full" v-model="template.templateName"/>
        </div>
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('ALERT_TYPE') }}</label>
          <vs-select autocomplete class="w-full" v-model="template.alertTypeEnumId">
            <vs-select-item v-for="(enumeration, index) in alertTypes" :key="index" :value="enumeration.enumId" :text="enumeration.description"/>
          </vs-select>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col w-full">
          <label class="vs-input--label">{{ $t('BODY') }}</label>
          <div class="code-editor">
            <codemirror v-model="template.body" :options="vueOptions"></codemirror>
          </div>
        </div>
      </div>
      <div class="nv-row mb-base" v-if="template.alertTypeEnumId === 'ALERT_EMAIL'">
        <div class="nv-col w-full">
          <label class="vs-input--label">{{ $t('STYLE') }}</label>
          <div class="code-editor">
            <codemirror v-model="template.style" :options="cssOptions"></codemirror>
          </div>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col w-full">
          <div class="flex justify-between">
            <vs-button class="px-5 py-3" color="primary" type="border" @click="cancel">{{ $t('CANCEL') }}</vs-button>
            <div class="dropdown-button-container">
              <vs-button class="btnx" type="filled" @click="modifyAlertTemplate('browse')">{{ $t('SUBMIT') }}</vs-button>
              <vs-dropdown vs-trigger-click>
                <vs-button class="btn-drop" type="filled" icon="fa-angle-down" icon-pack="far"></vs-button>
                <vs-dropdown-menu>
                  <vs-dropdown-item @click="modifyAlertTemplate('stay')">
                    <span>{{ $t('SUBMIT_STAY') }}</span>
                  </vs-dropdown-item>
                  <vs-dropdown-item @click="modifyAlertTemplate('view')">
                    <span>{{ $t('SUBMIT_VIEW') }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>
      </div>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import CodeEditorMixin from '@/mixins/commons/CodeEditorMixin'
import AlertTemplateService from '@/services/commons/AlertTemplateService'
import EnumerationService from '@/services/commons/EnumerationService'

export default {
  name: 'alert-template-modify',
  mixins: [AlertMixin, CodeEditorMixin],
  data: function () {
    return {
      alertTypes: [],
      template: {}
    }
  },
  methods: {
    async load () {
      let self = this
      AlertTemplateService.alertTemplates({
        templateId: this.$route.params.id
      }).then(function (response) {
        self.template = response.data.alertTemplateList[0] || {}
      })
      EnumerationService.enumerations({
        enumTypeId: 'AlertType',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.alertTypes = response.data.enumerationList || []
      })
    },
    modifyAlertTemplate (nextAction) {
      let self = this
      AlertTemplateService.modifyAlertTemplate({
        templateId: this.$route.params.id,
        templateName: this.template.templateName,
        alertTypeEnumId: this.template.alertTypeEnumId,
        body: this.template.body,
        style: this.template.style
      }).then(function (response) {
        if (response.status === 200) {
          if (nextAction === 'stay') {
            self.showSuccessAlert(response.data.messages)
          } else if (nextAction === 'view') {
            self.$router.push({ name: 'ViewAlertTemplate', params: { id: response.data.templateId } })
          } else {
            self.$router.push({ name: 'BrowseAlertTemplates' })
          }
        }
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted () {
    this.load()
  }
}
</script>
